.timeline {
  height: 36rem;
  width: 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  overflow: hidden;
}

.timeline span {
  display: block;
  color: white;
  position: absolute;
  font-size: .5rem; 
  font-weight: bold;
}

.timeline {
  animation: widthen .5s ease-out;
}

@keyframes widthen {
  from {
    width: 0;
  }
  to {
    width: 3rem;
  }
}
